rchive-property';\n@import 'pages/single-property';\n@import 'pages/post-type-archive-testimonial';\n@import 'pages/single-testimonial';\n\n@import 'bespoke';\n\nhtml {\n  scroll-behavior: auto !important;\n}\n\nbody,\nhtml {\n  /*width: 100vw;\n  overflow-x: hidden;\n  overscroll-behavior-y: none;*/\n}\n\n#main {\n  /*padding-top: 80px;*/\n}\n.container-fluid {\n  padding-left: 7.292vw;\n  padding-right: 7.292vw;\n}\n\n.page-template-template-master,\n.page-template-template-landing {\n  #main {\n    padding-top: 80px;\n  }\n}\n#infinite-handle {\n  margin: 0 !important;\n  width: 100%;\n  display: block;\n  margin: 0 auto;\n  grid-column: auto/span 12;\n  padding: 0 !important;\n\n  button {\n    padding: 0.45vw 0.9vw !important;\n    border: 1px solid #0f272d !important;\n    color: #0f272d !important;\n    border-radius: 1vw !important;\n    overflow: hidden !important;\n    line-height: 1 !important;\n\n    &:hover {\n      background: #0f272d !important;\n      color: #fff !important;\n    }\n  }\n}\n\n#ctis-loading {\n  margin: 0 !important;\n  width: 100%;\n  margin: 0 auto;\n  grid-column: auto/span 12;\n  padding: 0 !important;\n}\n\n.page-footer {\n  position: relative;\n}\n","@mixin bsBanner($file) {\n  /*!\n   * Bootstrap #{$file} v5.2.3 (https://getbootstrap.com/)\n   * Copyright 2011-2022 The Bootstrap Authors\n   * Copyright 2011-2022 Twitter, Inc.\n   * Licensed under MIT (https://github.com/twbs/bootstrap/blob/main/LICENSE)\n   */\n}\n\n",":root {\n  // Note: Custom variable values only support SassScript inside `#{}`.\n\n  // Colors\n  //\n  // Generate palettes for full colors, grays, and theme colors.\n\n  @each $color, $value in $colors {\n    --#{$prefix}#{$color}: #{$value};\n  }\n\n  @each $color, $value in $grays {\n    --#{$prefix}gray-#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors {\n    --#{$prefix}#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors-rgb {\n    --#{$prefix}#{$color}-rgb: #{$value};